import { customRef } from 'vue'

export default function useDebouncedRef(value, delay = 200) {
    let timer = null
    return customRef((track, tragger) => {
        return {
            get() {
                track()
                return value
            },
            set(newV) {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    value = newV
                    tragger()
                }, delay)
            }
        }
    })
}
