<template>
    <div>
        <div>{{obj.counter}}</div>
        <button @click="incream">+</button>
        <input v-model="text">
        <div>{{text}}</div>
    </div>
</template>
<script>
    import axios from 'axios'
    // import { ref, reactive, readonly } from 'vue'
    import debouncedRef from './debounceRef.js'
    import { reactive, ref, onBeforeMount } from 'vue'
    export default {
        setup(props, context) {
            console.log(props, context)
            onBeforeMount(() => {
                axios.get('/wxApi/login?name=张三&age=18').then(res => {
                    console.log(res)
                })
            })
            let obj = reactive({})
            let counter = ref(1)
            obj.counter = counter
            const incream = () => {
                obj.counter++
                console.log(obj.counter === counter.value)
                console.log(obj.counter)
                console.log(counter.value)
            }
            return {
                obj,
                
                text: debouncedRef(''),
                incream
            }
        }
    }
</script>
